import React, { useState } from 'react';
import ConfettiExplosion from 'react-confetti-explosion';
import './WinPage.css';
import { useNavigate } from 'react-router-dom';

const cards = [
  {
    id: 1,
    name: 'Куда, зачєм і пачєму?',
    image: 'assets/picnic.jpg',
    menu: ['Бутербродікі', ' чіпсікі', ' фрукти', ' торт', 'бургери'],
    locations: ['Набережна', ' пляж набережної', ' ботанчіний сад'],
    entertainment: ['Годувати лебедів', ' дивитись на річку', ' лежати на пледі']
  },
  {
    id: 2,
    name: 'СРай (Сашо-Рай)',
    image: 'assets/burgers.jpg',
    menu: ['Бургери', ' картошечка', ' Кока-Колочка'],
    locations: ['Парк', ' набережна', ' True Burger Bar'],
    entertainment: ['Погуляти', ' наїстись', ' подивитись серіал', ' пограти в комп']
  },
  {
    id: 3,
    name: 'Я надто старий для ваших вечірок',
    image: 'assets/home.jpg',
    menu: ['Піцца', ' бургери', ' Кока-Колочка'],
    locations: ['Спальня', ' сусідня кімната', ' кухня'],
    entertainment: ['12 фільмів за добу', ' I love Steam Deck more than everything', ' настільні ігри']
  },
  {
    id: 4,
    name: 'Дєди штурмуют Dream-town',
    image: 'assets/dream-town.jpg',
    menu: ['Мак', ' Мак', ' Мак'],
    locations: ['Dream town'],
    entertainment: ['ролики/ковзани', ' дзеркальний лабіринт', ' автомати', ' батут']
  }
];

export const WinPage = () => {
  const [isExploding, setIsExploding] = useState(true);
  const [selectedCard, setSelectedCard] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const navigate = useNavigate();

  const handleCloseModal = () => {
    setShowModal(false);
  };

  const handleCardClick = (card) => {
    setSelectedCard(card);
    setShowModal(true);
    setIsClicked(true);
  }

  return (
    <div className='win-page'>
      <h2>ПЕРЕМОГА! А тепееер,</h2>
      <h2>Куди підем?</h2>
      {isExploding && <ConfettiExplosion />}
      <div className="cards">
        {cards
          .sort(() => Math.floor(Math.random() * (3 - 0) + 0))
          .map((card) => (
            <div
              key={card.id}
              onClick={() => handleCardClick(card)}
              className={`card-win`}
            >
            </div>
          ))}
      </div>
      {isExploding && <ConfettiExplosion />}
      {selectedCard ? (
        <div className="card-container">
          {showModal && (
            <div className="modal">
              <div className="modal-content">
                <button className="close-button" onClick={handleCloseModal}>
                  X
                </button>
                <h2>{selectedCard.name}</h2>
                <div>
                  <img src={selectedCard.image} alt="card" className='card-win-image' />
                  <div>
                    <p>{`Меню: ${selectedCard.menu}`}</p>
                    <p>{`Де: ${selectedCard.locations}`}</p>
                    <p>{`Ентертеймент: ${selectedCard.entertainment} `}</p>
                    <br />
                    <pre>* Якщо не подобається картка, можеш обрати іншу або додати свої побажання :)</pre>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      ) : null}

      {isClicked &&
        <div className='button-class'>
          <h1
            className='next'
            onClick={() => { navigate('/wish') }}
          >
            Next!
          </h1>
        </div>
      }
    </div>
  )
}