import React, { useEffect, useRef } from 'react';
import './WishPage.css';

const foresights = [
  'Шоб тебе обходили проблеми як цей квадрат кути ♥',
  'Шоб Alina4ka ніколи не злилась на тебе ♥',
  'Шоб не ти думав про kubernetes, а він про тебе ♥',
  'Шоб Біг-мак меню тебе знайшло, де б ти не був ♥',
];

const getRandomNumber = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};
const randomForesight = foresights[getRandomNumber(0, foresights.length)];
export const WishPage = () => {
  const imageRef = useRef(null);
  let x = 0;
  let y = 0;
  let dx = 2;
  let dy = 2;


  const moveImage = () => {
    const image = imageRef.current;
    const { width, height } = image.getBoundingClientRect();
    const { clientWidth, clientHeight } = document.documentElement;

    x += dx;
    y += dy;

    if (x + width > clientWidth || x < 0) {
      dx = -dx;
    }

    if (y + height > clientHeight || y < 0) {
      dy = -dy;
    }

    image.style.transform = `translate3d(${x}px, ${y}px, 0)`;

    requestAnimationFrame(moveImage);
  };

  useEffect(() => {
    moveImage();
  }, []);


  return (
    <div className="wish-page">
      <img
        className="moving-image"
        src="favicon.jpg"
        alt="Birthday"
        ref={imageRef} 
        />
      <div className='wish-block'
      >
        <h1 className='wish-title'>Happy Birthday, Ca4ok!</h1>
        <p className='wish-text'>Побажання на наступний рік: {randomForesight}</p>
      </div>
    </div>
  );
};




