import React, { useState, useEffect, useRef } from 'react';
import './memoryCard.css';
import ConfettiExplosion from 'react-confetti-explosion';
import { useNavigate } from 'react-router-dom';
import ReactAudioPlayer from 'react-audio-player';

const cardImages = [
  'assets/1.jpg',
  'assets/2.jpg',
  'assets/3.jpg',
  'assets/4.jpg',
  'assets/5.jpg',
  'assets/6.jpg',
  'assets/7.jpg',
  'assets/8.jpg',
  'assets/9.jpg',
  'assets/10.jpg',
];

export const MemoryCard = () => {
  const [cards, setCards] = useState([]);
  const [flippedCards, setFlippedCards] = useState([]);
  const [matchedCards, setMatchedCards] = useState([]);
  const [isExploding, setIsExploding] = React.useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const newCards = cardImages
      .concat(cardImages)
      .sort(() => Math.random() - 0.5)
      .map((image, index) => ({
        id: index,
        image,
        flipped: false,
        color: getRandomColor(),
      }));
    setCards(newCards);


  }, []);

  useEffect(() => {
    if (flippedCards.length === 2) {
      const [card1, card2] = flippedCards;
      if (card1.image === card2.image) {
        setIsExploding(true)
        setMatchedCards([...matchedCards, card1.id, card2.id]);
        setFlippedCards([]);
      } else {
        setTimeout(() => {
          setCards(
            cards.map((card) =>
              card.id === card1.id || card.id === card2.id ? { ...card, flipped: false } : card
            )
          );
          setFlippedCards([]);
        }, 500);
      }
    }
    if (matchedCards.length === cards.length && matchedCards.length > 0) {
      navigate('/win');
    }
  }, [flippedCards]);

  const handleCardClick = (card) => {
    setIsExploding(false)
    if (matchedCards.includes(card.id) || flippedCards.length === 2 || card.flipped) {
      return;
    }
    setFlippedCards([...flippedCards, card]);
    setCards(cards.map((c) => (c.id === card.id ? { ...c, flipped: true } : c)));
  };

  function getRandomColor() {
    const r = Math.floor(Math.random() * 256);
    const g = Math.floor(Math.random() * 256);
    const b = Math.floor(Math.random() * 256);
    return `rgb(${r}, ${g}, ${b})`;
  }

  const [isPlaying, setIsPlaying] = useState(true);
  let song = useRef(null);

  const togglePlay = (value) => {
    if (value) {
      song.audioEl.current.play()
    } else {
      song.audioEl.current.pause()
    }
    setIsPlaying(!isPlaying);
    console.log(song.audioEl.current)
  }


  return (
    <div className="memory-game">
      <h1 className='memory-title'>Find all the pairs</h1>
      <div className='music'>
        <h1
          className='audio'
          onClick={() => { togglePlay(isPlaying) }}
        >
          Click me!
        </h1>
        {!isPlaying && <ConfettiExplosion />}
        <ReactAudioPlayer
          src="Preach.mp3"
          autoPlay={false}
          controls={false}
          loop
          ref={(element) => { song = element; }}
        />
      </div>
      {isExploding && <ConfettiExplosion />}
      <div className="cards">
        {cards.map((card) => (
          <div
            key={card.id}
            onClick={() => handleCardClick(card)}
            className={`card ${card.flipped || matchedCards.includes(card.id) ? 'flipped' : ''}`}
          >
            <div className="front"></div>
            <div className="back">
              {card.flipped &&
                <img
                  style={{ border: `5px solid ${card.color}` }}
                  src={card.image}
                  alt="img"
                  className='card-image'
                />
              }
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

